import React from 'react'
import { BrowserRouter as Router, Switch,Route} from "react-router-dom";
import { FormControl, Navbar, Nav, Button, Form, Card, CardGroup  } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import panel from '../img/panel.JPG';
import otro from '../img/otro.JPG';
import {Home} from './Home'
import {Deportistas} from './Deportistas'
import {Nosotros} from './Nosotros'
import {Video} from './Video'

const Menu = () => {
return (
 <Router>

     <Navbar bg="warning" variant="light">
         <Navbar.Brand  className="col-3" href="/">Home</Navbar.Brand>
         <Nav className="mr-auto">
             <Nav.Link className="" href="/IconosFifa22/">Iconos</Nav.Link>
             <Nav.Link href="/NuevasCaracteristicas/">Caracteristicas</Nav.Link>
             <Nav.Link href="/TrailerOficial">Video</Nav.Link>


         </Nav>
     </Navbar>
     <Switch>
         <Route path ="/" exact component={Home}/>
          <Route path ="/IconosFifa22/"  component={Deportistas} />
           <Route path= "/NuevasCaracteristicas/"  component={Nosotros}  />
             <Route path= "/TrailerOficial/"  component={Video} />
    
     </Switch>
 </Router>




)

     
         
}
export default Menu;